.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (max-width: 767px) {
    overflow-y: scroll;
  }
}
.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%) scale(1.2);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 100%;
  height: 100%;
  overflow: auto;
  padding-top: 48px;
  padding-bottom: 40px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: $bg-first-color;

  transform: translate(-50%, -50%), scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: $mobile) {
    width: 280px;
    height: 958px;
  }

  @media screen and (min-width: $tablet) {
    width: 618px;
    height: 562px;
    padding-top: 42px;
    padding-left: 30px;
    padding-right: 30px;
  }

  @media screen and (min-width: $desktop) {
    width: 882px;
    height: 568px;
    padding-top: 48px;
    padding-bottom: 41px;
  }

  &__title {
    margin-bottom: 20px;
    font-size: 20px;
    line-height: 1.15;
    text-transform: uppercase;

    @media screen and (min-width: $desktop) {
      font-size: 30px;
      line-height: 1.16;
    }
  }
}
.modal__content {
  @media screen and (min-width: $tablet) {
    display: flex;
  }
}
// -----изображение
.thumb {
  width: 240px;
  height: 356px;
  border-radius: 5px;
  overflow: hidden;

  @media screen and (min-width: $tablet) {
    width: 264px;
    height: 373px;
  }
  @media screen and (min-width: $desktop) {
    width: 396px;
    height: 477px;
  }
}
.modal__image {
  width: 100%;
  height: auto;
}

// ------текстовая часть
.wrap-modal {
  display: flex;
}

.wrap-modal ul:not(:last-child) {
  margin-right: 30px;
}

.content__text {
  margin-top: 30px;
  width: 240px;

  @media screen and (min-width: $tablet) {
    margin-left: 30px;
    margin-top: 0px;
    width: 264px;
  }
  @media screen and (min-width: $desktop) {
    width: 396px;
  }
}
.modal__list {
  margin-bottom: 20px;
  font-size: 12px;
  line-height: 1.33;
  color: $modal-subtitle-color;
}

.modal__list--keys {
  width: 122px;
}
.modal__list--values {
  color: $text-black-color;
}
.modal__list--values li:nth-child(-n + 2) {
  line-height: 1.16;
}
.modal-list__item {
  display: flex;
}
.modal__list li:not(:last-child) {
  margin-bottom: 10px;
}

.modal__rating {
  line-height: 14px;
  height: 16px;
  width: 36px;
  padding-top: 1px;
  text-align: center;
  background-color: $bg-second-color;
  border-radius: $radius;
}

.modal__rating--orange {
  color: $text-white-color;
  line-height: 14px;
  height: 16px;
  width: 36px;
  padding-top: 1px;
  text-align: center;
  background-color: $accent-lighter-color;
  border-radius: $radius;
}

.modal__rating--slash {
  line-height: 14px;
  height: 16px;
  padding-top: 1px;
  padding-left: 3px;
  padding-right: 3px;
}

.modal-text__title {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;
}
.modal-text__item {
  margin-bottom: 21px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
}

.list-buttons {
  display: flex;
}
.list-buttons li:not(:last-child) {
  margin-right: 15px;
}
.button-modal {
  padding-top: 14px;
  padding-bottom: 12px;
  border-radius: 4px;
  min-width: 112px;
  min-height: 44px;

  border: 1px solid $black-color;
  border-radius: 5px;

  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: $tablet) {
    min-width: 125px;
  }

  @media screen and (min-width: $desktop) {
    min-width: 136px;
  }

  &:hover,
  &:focus {
    background-color: $accent-lighter-color;
    color: $text-white-color;
    border: none;
  }
}
.button-modal--orange {
  background: $accent-lighter-color;
  border: none;
  color: $text-white-color;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $bg-first-color;
    border: 1px solid $black-color;
    color: $text-black-color;
  }
}

.button-modal--close {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 30px;
  height: 30px;
}

